<template>
  <div class="row">
    <div class="col-12 mt-3" v-if="isReady">
      <div class="row">
        <div class="col-12 flex-x-between mb-3">
          <div class="flex-align-center">
            <el-button
              v-ripple.400
              type="primary"
              circle
              class="no-width flat"
              @click="$router.go(-1)"
            >
              <i class="bx bx-left-arrow-alt"></i>
            </el-button>
            <h5 class="my-0 ml-2">New {{ finance.type }}</h5>
            <el-tag
              type="success"
              class="rounded ml-2"
              v-if="finance.type == 'Incomes'"
              ><i class="bx bx-down-arrow-circle"></i>
              {{ finance.type }}</el-tag
            >
            <el-tag
              type="danger"
              class="rounded ml-2"
              v-if="finance.type == 'Expense'"
              ><i class="bx bx-up-arrow-circle"></i>{{ finance.type }}</el-tag
            >
          </div>
          <div class="flex-align-center flex-end">
            <el-button
              @click="$router.go(-1)"
              type="primary"
              v-ripple.400
              class="flat"
            >
              Discard
            </el-button>
            <el-button
              :loading="saving"
              v-ripple.400
              @click="Post"
              type="primary"
            >
              Save
            </el-button>
          </div>
        </div>
        <div class="col-lg-8">
          <div class="box">
            <p class="mt-0 mb-2 text-bold">Details</p>

            <div class="row">
              <div class="col-lg-6">
                <label for="name" class="custom-label">Note</label>
                <el-input
                  name="name"
                  class="w-100 mb-3"
                  v-model="finance.note"
                />
              </div>
              <div class="col-lg-6">
                <label for="name" class="custom-label">Label</label>
                <el-select
                  v-model="finance.label_name"
                  filterable
                  clearable
                  allow-create
                  class="w-100"
                  placeholder="Label"
                >
                  <el-option
                    v-for="label in labels"
                    :key="label.id"
                    :label="label.name"
                    :value="label.name"
                  >
                  </el-option>
                </el-select>
              </div>
              <div class="col-lg-6">
                <label for="date" class="custom-label">Date</label>
                <el-date-picker
                  class="w-100"
                  v-model="finance.date"
                  type="date"
                  format="dd/MM/yyyy"
                  value-format="dd/MM/yyyy"
                  placeholder="Pick a date"
                >
                </el-date-picker>
              </div>
              <div class="col-lg-6">
                <label for="Phone" class="custom-label">Amount</label>
                <el-input
                  name="Amount"
                  type="number"
                  class="w-100 mb-3"
                  v-model="finance.amount"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  data() {
    return {
      saving: false,
      finance: {
        label_name: "Label name",
      },
      loading: null,
      startEditing: true,
      financeBeforeEdit: "",
      labels: [],
    };
  },
  watch: {
    finance: {
      handler: function (val) {
        if (this.financeBeforeEdit == JSON.stringify(val)) {
          this.startEditing = false;
        } else {
          this.startEditing = true;
        }
      },
      deep: true,
    },
  },
  created() {
    this.finance.type = this.$route.params.type;
    this.getLabels();
    this.$store.commit("setIsReady", true);
    this.finance.date = this.$moment().format("DD/MM/YYYY");
  },
  beforeMount() {
    window.addEventListener("beforeunload", this.preventNav);
    this.$once("hook:beforeDestroy", () => {
      window.removeEventListener("beforeunload", this.preventNav);
    });
  },
  beforeRouteLeave(to, from, next) {
    if (this.startEditing) {
      this.$smalltalk
        .confirm(
          "Question",
          "Do you really want to leave? you have unsaved changes!"
        )
        .then(() => {
          next();
        })
        .catch(() => {
          next(false);
        });
    } else {
      next();
    }
  },
  methods: {
    preventNav(event) {
      if (!this.startEditing) return;
      event.preventDefault();
      event.returnValue = "";
    },
    getLabels() {
      var url = `labels`;
      this.$axios
        .get(url)
        .then((response) => {
          this.labels = response.data;
        })
        .catch((err) => {
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
    Post() {
      this.saving = true;
      this.$axios
        .post("finance/", { data: this.finance })
        .then(() => {
          this.saving = false;
          this.$noti.display({
            title: "Success",
            status: "normal",
            color: "success",
            message: "Cool! Finance has been successfully created.",
          });
          this.startEditing = false;
          this.$router.push("/finance");
        })
        .catch((err) => {
          this.saving = false;
          this.$noti.display({
            message: err,
            title: "",
            status: "error",
            color: "error",
          });
        });
    },
  },
  computed: {
    ...mapState(["isReady", "store"]),
  },
};
</script>

